import { Live2DCubismFramework as cubismmatrix44 } from "./Framework/math/cubismmatrix44";
import { Live2DCubismFramework as csmvector } from "./Framework/type/csmvector";
import { Live2DCubismFramework as acubismmotion } from "./Framework/motion/acubismmotion";
import { Live2DCubismFramework as cubismframework } from './Framework/live2dcubismframework';
import {jsonPath , modelDir, modelName} from './lappdelegate'
import Csm_CubismMatrix44 = cubismmatrix44.CubismMatrix44;
import ACubismMotion = acubismmotion.ACubismMotion;


import { LAppModel } from "./lappmodel";
import { LAppPal } from "./lapppal";
import { canvas,zoom,modelX,modelY } from "./lappdelegate";
import * as LAppDefine from "./lappdefine";

export let s_instance: LAppLive2DManager = null;


// v2
let models = [];
let count = -1;
let reloadFlg = false;

/**
 * CubismModel
 * 
 */
export class LAppLive2DManager {
  /**
   * 
   * 
   * LAppLive2DManager 
   * let a = new LAppLive2DManager(); LAppLive2DManager.getInstance();
   *
   * @return 
   */
  public static getInstance(): LAppLive2DManager {
    if (s_instance == null) {
      s_instance = new LAppLive2DManager();
    }
    return s_instance;
  }

  /**
   * 
   */
  public static releaseInstance(): void {
    if (s_instance != null) {
      s_instance = void 0;
    }

    s_instance = null;
  }


  /**
   * 
   */
  public releaseAllModel(): void {
    this._model.release()
  }

  /**
   * 
   *
   * @param x X
   * @param y Y
   */
  public onDrag(x: number, y: number): void {
    if (this._model) {
      this._model.setDragging(x, y);
    }
  }

  /**
   * 
   * 
   *
   * @param x X
   * @param y Y
   */
  public onTap(x: number, y: number ,hitAreaList :any[] ): void {
    if (LAppDefine.DebugLogEnable) {
      LAppPal.printMessage(
        `[APP]tap point: {x: ${x.toFixed(2)} y: ${y.toFixed(2)}}`
      );
    }
   
    if (this._model.hitTest(LAppDefine.HitAreaNameHead, x, y)) {
      if (LAppDefine.DebugLogEnable) {
        LAppPal.printMessage(`[APP]hit area: [${LAppDefine.HitAreaNameHead}]`);
      }
      this._model.setRandomExpression();
    } else if (this._model.hitTest(LAppDefine.HitAreaNameBody, x, y)) {
      if (LAppDefine.DebugLogEnable) {
        LAppPal.printMessage(`[APP]hit area: [${LAppDefine.HitAreaNameBody}]`);
      }
      this._model.startRandomMotion(
        LAppDefine.MotionGroupTapBody,
        LAppDefine.PriorityNormal,
        this._finishedMotion
      );
    }else {
      // 
      for(let i = 0 ;i < hitAreaList.length; i++){
        if(this._model.hitTestDefine(hitAreaList[i], x, y)){ //
          if (LAppDefine.DebugLogEnable) {
            LAppPal.printMessage(`[APP]hit area: [${name}]`);
          }
          this._model.startMotionFile(
            hitAreaList[i],
            LAppDefine.PriorityNormal, //
            this._finishedMotion
          );
        }
      }
    }
  }

  /**
   * 
   * 
   */
  public onUpdate(): void {
    let projection: Csm_CubismMatrix44 = new Csm_CubismMatrix44();
    const { width, height } = canvas;
    //XY
    projection.translate(modelX/100,modelY/100);
    // v2 main.js  97 
    //  canvas 
    // 
    projection.scale(zoom, (zoom * width) / height);

    if (this._viewMatrix != null) {
      projection.multiplyByMatrix(this._viewMatrix);
    }

    const saveProjection: Csm_CubismMatrix44 = projection.clone();

    const model: LAppModel = this._model;
    projection = saveProjection.clone();

    model.update();
    model.draw(projection); // projection
  }

  

  /**
   * 
   */
  constructor() {
    // DefineJsonPath 
    //+1/
    const modelHomeDir = modelDir;
    this._viewMatrix = new Csm_CubismMatrix44();
    this._model = new LAppModel();
    const modelJsonName = `${modelName}.model3.json`;
    this._model.loadAssets(modelHomeDir, modelJsonName);
  }

  _viewMatrix: Csm_CubismMatrix44; // view
  _model: LAppModel; // 
  // 
  _finishedMotion = (self: ACubismMotion): void => {
    if(LAppDefine.DebugLogEnable){
      LAppPal.printMessage("Motion Finished:");
      console.log(self);
    }
  };
  
}


