import {
  Live2DCubismFramework as live2dcubismframework,
  Option as Csm_Option,
} from "./Framework/live2dcubismframework";
import Csm_CubismFramework = live2dcubismframework.CubismFramework;
import { LAppView } from "./lappview";
import { LAppPal } from "./lapppal";
import { LAppTextureManager } from "./lapptexturemanager";
import { LAppLive2DManager } from "./lapplive2dmanager";
import * as LAppDefine from "./lappdefine";

//import { config } from "./config/configMgr";
//import { L2Dwidget } from "./index";

export let canvas: HTMLCanvasElement = null;
export let s_instance: LAppDelegate = null;
export let gl: WebGLRenderingContext = null;
export let frameBuffer: WebGLFramebuffer = null;

export let jsonPath: string = null;
export let modelName: string = null;
export let modelDir: string = null;

export let zoom: number ;
export let modelX: number;
export let modelY: number;

/**
 * 
 * Cubism SDK
 */
export class LAppDelegate {
  /**
   * 
   * 
   * LAppDelegate 
   * let a = new LAppDelegate(); LAppDelegate.getInstance();
   *
   * @return LAppDelegate
   */
  public static getInstance(): LAppDelegate {
    if (s_instance == null) {
      s_instance = new LAppDelegate();
    }

    return s_instance;
  }

  /**
   * 
   */
  public static releaseInstance(): void {
    if (s_instance != null) {
      s_instance.release();
    }

    s_instance = null;
  }


  /**
   * APP
   * Live2D
   */
  public initialize(canvasId:string,path:string): boolean {
    
    canvas =  document.getElementById(canvasId) as HTMLCanvasElement;
    jsonPath = path
    modelName = LAppDefine.path2ModelName(path);
    modelDir = LAppDefine.pathName(path);
    zoom = this._zoom;
    modelX = this._modelX;
    modelY = this._modelY;
    const supportTouch: boolean = "ontouchend" in canvas;
    if (supportTouch) {
      // 
      canvas.ontouchstart = onTouchBegan;
      window.ontouchmove = onTouchMoved;
      canvas.ontouchend = onTouchEnded;
      canvas.ontouchcancel = onTouchCancel;
    } else {
      // 
      canvas.onmousedown = onClickBegan;
      window.onmousemove = onMouseMoved;
      canvas.onmouseup = onClickEnded;
      document.onmouseleave = onMouseLeave;
    }

    // @ts-ignore
    gl =  getWebGLContext(canvas);

    if (!gl) {
      alert("Cannot initialize WebGL. This browser does not support.");
      gl = null;

      document.body.innerHTML =
        "This browser does not support the <code>&lt;canvas&gt;</code> element.";

      // gl
      return false;
    }

    if (!frameBuffer) {
      frameBuffer = gl.getParameter(gl.FRAMEBUFFER_BINDING);
    }

    // 
    gl.enable(gl.BLEND);
    gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);


    // AppView
    this._view.initialize();

    // Cubism SDK
    this.initializeCubism();

    return true;
  }

  /**
   * 
   */
  public release(): void {
    this._textureManager.release();
    this._textureManager = null;

    this._view.release();
    this._view = null;

    // 
    LAppLive2DManager.releaseInstance();

    // Cubism SDK
    Csm_CubismFramework.dispose();
  }

  /**
   * 
   */
  public run(): void {
    // 
    const loop = (): void => {
      // 
      if (s_instance == null) {
        return;
      }

      // 
      LAppPal.updateTime();

      // 
      // RGBA 0-1
      gl.clearColor(0.0, 0.0, 0.0, 0.0);

      // 
      gl.enable(gl.DEPTH_TEST);

      // 
      gl.depthFunc(gl.LEQUAL);

      // 
      // 
      gl.clear(gl.COLOR_BUFFER_BIT | gl.DEPTH_BUFFER_BIT);

      gl.clearDepth(1.0);

      // 
      gl.enable(gl.BLEND);
      gl.blendFunc(gl.SRC_ALPHA, gl.ONE_MINUS_SRC_ALPHA);

      this._view._head_pos = this._head_pos;
      this._view._hitAreaList = this._hitAreaList;

      // 
      this._view.render();
      // 
      if(this._captureFrame){
        this._captureFrame = false;
        var link = document.createElement('a');
        document.body.appendChild(link);
        link.setAttribute("type", "hidden");
        link.href = canvas.toDataURL();
        link.download = this._captureName || 'live2d.png';
        link.click();
      }

      // 
      requestAnimationFrame(loop);
    };
    loop();
  }

  /**
   * 
   */
  public createShader(): WebGLProgram {
    // 
    const vertexShaderId = gl.createShader(gl.VERTEX_SHADER);

    if (vertexShaderId == null) {
      LAppPal.printMessage("failed to create vertexShader");
      return null;
    }

    const vertexShader: string =
      "precision mediump float;" +
      "attribute vec3 position;" +
      "attribute vec2 uv;" +
      "varying vec2 vuv;" +
      "void main(void)" +
      "{" +
      "   gl_Position = vec4(position, 1.0);" +
      "   vuv = uv;" +
      "}";

    gl.shaderSource(vertexShaderId, vertexShader);
    gl.compileShader(vertexShaderId);

    // 
    const fragmentShaderId = gl.createShader(gl.FRAGMENT_SHADER);

    if (fragmentShaderId == null) {
      LAppPal.printMessage("failed to create fragmentShader");
      return null;
    }

    const fragmentShader: string =
      "precision mediump float;" +
      "varying vec2 vuv;" +
      "uniform sampler2D texture;" +
      "void main(void)" +
      "{" +
      "   gl_FragColor = texture2D(texture, vuv);" +
      "}";

    gl.shaderSource(fragmentShaderId, fragmentShader);
    gl.compileShader(fragmentShaderId);

    // 
    const programId = gl.createProgram();
    gl.attachShader(programId, vertexShaderId);
    gl.attachShader(programId, fragmentShaderId);

    gl.deleteShader(vertexShaderId);
    gl.deleteShader(fragmentShaderId);

    // 
    gl.linkProgram(programId);

    gl.useProgram(programId);

    return programId;
  }

  /**
   * View
   */
  public getView(): LAppView {
    return this._view;
  }

  public getTextureManager(): LAppTextureManager {
    return this._textureManager;
  }

  /**
   * 
   */
  constructor() {
    this._captured = false;
    this._captureFrame = false;
    this._mouseX = 0.0;
    this._mouseY = 0.0;
    this._modelX = 0.0;
    this._modelY = 0.0;
    this._isEnd = false;

    this._cubismOption = new Csm_Option();
    this._view = new LAppView();
    this._textureManager = new LAppTextureManager();
    this._zoom = 1.0;
    this._head_pos = 0.5;
  }

  /**
   * Cubism SDK
   */
  public initializeCubism(): void {
    // setup cubism
    this._cubismOption.logFunction = LAppPal.printMessage;
    this._cubismOption.loggingLevel = LAppDefine.CubismLoggingLevel;
    Csm_CubismFramework.startUp(this._cubismOption);

    // initialize cubism
    Csm_CubismFramework.initialize();

    // LAppLive2DManagerjsonPath
    LAppLive2DManager.getInstance();

    LAppPal.updateTime();

    this._view.initializeSprite();
  }

  _cubismOption: Csm_Option; // Cubism SDK Option
  _view: LAppView; // View
  _captured: boolean; // 
  _captureFrame: boolean;
  _mouseX: number; // X
  _mouseY: number; // Y
  _isEnd: boolean; // APP
  _textureManager: LAppTextureManager; // 
  _zoom:number;//
  _captureName:string;//
  _head_pos:number;//
  _hitAreaList : any[];
  _modelX: number;
  _modelY: number;
}

/**
 * ---------------------------------------------------
 * 
 * 
 */
function onClickBegan(e: MouseEvent): void {
  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }
  LAppDelegate.getInstance()._captured = true;

  const posX: number = e.pageX;
  const posY: number = e.pageY;

  LAppDelegate.getInstance()._view.onTouchesBegan(posX, posY);
}

/**
 * 
 */
function onMouseMoved(e: MouseEvent): void {
  // if (!LAppDelegate.getInstance()._captured) {
  //   return;
  // }

  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  const rect = canvas.getBoundingClientRect()

  LAppDelegate.getInstance()._view.onTouchesMoved(e.clientX,e.clientY,rect);
}

/**
 * 
 */
function onClickEnded(e: MouseEvent): void {
  LAppDelegate.getInstance()._captured = false;
  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  const rect = (e.target as Element).getBoundingClientRect();
  const posX: number = e.clientX - rect.left;
  const posY: number = e.clientY - rect.top;

  LAppDelegate.getInstance()._view.onTouchesEnded(posX, posY);
}

/**
 * 
 */
function onTouchBegan(e: TouchEvent): void {
  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  LAppDelegate.getInstance()._captured = true;

  const posX = e.changedTouches[0].pageX;
  const posY = e.changedTouches[0].pageY;

  LAppDelegate.getInstance()._view.onTouchesBegan(posX, posY);
}

/**
 * 
 */
function onTouchMoved(e: TouchEvent): void {
  if (!LAppDelegate.getInstance()._captured) {
    return;
  }

  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  const rect = (e.target as Element).getBoundingClientRect();

  const posX = e.changedTouches[0].clientX;
  const posY = e.changedTouches[0].clientY;

  LAppDelegate.getInstance()._view.onTouchesMoved(posX,posY, rect);
}

/**
 * 
 */
function onTouchEnded(e: TouchEvent): void {
  LAppDelegate.getInstance()._captured = false;

  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  const rect = (e.target as Element).getBoundingClientRect();

  const posX = e.changedTouches[0].clientX - rect.left;
  const posY = e.changedTouches[0].clientY - rect.top;

  LAppDelegate.getInstance()._view.onTouchesEnded(posX, posY);
}

/**
 * 
 */
function onTouchCancel(e: TouchEvent): void {
  LAppDelegate.getInstance()._captured = false;

  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }

  const rect = (e.target as Element).getBoundingClientRect();

  const posX = e.changedTouches[0].clientX - rect.left;
  const posY = e.changedTouches[0].clientY - rect.top;

  LAppDelegate.getInstance()._view.onTouchesEnded(posX, posY);
}

function onMouseLeave(e: MouseEvent): void {
  LAppDelegate.getInstance()._captured = false;
  if (!LAppDelegate.getInstance()._view) {
    LAppPal.printMessage("view notfound");
    return;
  }
  LAppDelegate.getInstance()._view.onTouchesEnded(0, 0);
}

/**
 * WebGLContext
 * @param canvas canvas
 */
function getWebGLContext(canvas)
{
    var NAMES = [ "webgl" , "experimental-webgl" , "webkit-3d" , "moz-webgl"];
    for( var i = 0; i < NAMES.length; i++ ){
        try{
            var ctx = canvas.getContext(NAMES[i], {premultipliedAlpha : true});
            if(ctx) return ctx;
        }
        catch(e){}
    }
    return null;
}



