  /**
   * 
   * @param path {}.model3.json
   * @returns 
   */
   export function path2ModelName(path:string):string{
    let jsonName = path2FileName(path);
    return jsonName.substring(0,jsonName.indexOf('.'));
  }
  /**
   * 
   * @param path model3.json
   * @returns 
   */
  export function pathName(path:string):string{
    return path.substring(0,path.lastIndexOf('/') + 1);
  }

  export function path2FileName(path:string):string{
    return path.substring(path.lastIndexOf('/') + 1);
  }

  export function path2DirName(path:string):string{
    var url = path.substring(path.indexOf('//') + 2);
    let hasIndex = path.indexOf("model3.json");
    if(hasIndex>0){
      //
      let dirList = url.split('/');
      return `${dirList[dirList.length -2]}.moc3`;
    }else{
      let dirList = url.split('/');
      if(url.indexOf(".mtn")>0){
        //MTN
        return dirList[dirList.length -3];
      }else if(url.indexOf(".moc")>0){
        //MTN
        return dirList[dirList.length -2];
      }else{
        return dirList[dirList.length -1];
      }
    }
  }