/**
 *  Cubism Platform Abstraction Layer.
 *
 * 
 * 
 * 
 */
export class LAppPal {
  /**
   * 
   *
   * @param filePath 
   * @return
   * {
   *      buffer,   
   *      size        
   * }
   */
  public static loadFileAsBytes(
    filePath: string,
    callback: (arrayBuffer: ArrayBuffer, size: number) => void
  ): void {
    fetch(filePath)
      .then(response => response.arrayBuffer())
      .then(arrayBuffer => callback(arrayBuffer, arrayBuffer.byteLength));
  }

  /**
   * 
   * @return [ms]
   */
  public static getDeltaTime(): number {
    return this.s_deltaTime;
  }

  public static updateTime(): void {
    this.s_currentFrame = Date.now();
    this.s_deltaTime = (this.s_currentFrame - this.s_lastFrame) / 1000;
    this.s_lastFrame = this.s_currentFrame;
  }

  /**
   * 
   * @param message 
   */
  public static printMessage(message: string): void {
    console.log(message);
  }

  public static modelUrlVersion(modelurl:string):number{
    let setting:any = null;
    fetch(modelurl,{
      mode :'cors',
      method: "GET"
    })
    .then(function(response){
      if(response.status === 200){
        setting = response.arrayBuffer;
      }
    });
    let test = JSON.stringify(setting);
    return 2;
  }

  static lastUpdate = Date.now();

  static s_currentFrame = 0.0;
  static s_lastFrame = 0.0;
  static s_deltaTime = 0.0;
}
