import {getContext,path2FileName,path2DirName} from "./lappdefine"
import { CubismJson } from './Framework/utils/cubismjson'
import { db } from "./DbCache";

let requestCache = {};

export class PlatformManager{
    /**
     * PlatformManager # loadBytes()
     * @param path 
     */
    public async loadBytes(path:string) : Promise<ArrayBuffer>{
        // model
        let fileKey = path2FileName(path);
        let fileName =path2DirName(path);
        console.log('',fileName);
        // Cache 
        let rsp = await db.mocJson.where({key:fileKey,name:fileName}).first();
        if(rsp){
            return rsp.value;
        }else{
            let request = await fetch(path);
            let rsp =  await request.arrayBuffer();
            switch(request.status){
                case 200:
                    db.mocJson.put({key:fileKey,name:fileName,value :rsp});
                    return rsp;
                default:
                    console.error("Failed to load (" + request.status + ") : " + path);
                    return null!;
            }
        }
    }

    /**
     * 
     * @param path 
     * @param callback 
     */
    public async loadString(path:string):Promise<string>{
        let buf = await this.loadBytes(path);
        let arrayStr = CubismJson.arrayBufferToString(buf);
        return arrayStr;
    }

    public loadLive2DModel(path:string, callback:any){
        this.loadBytes(path).then((buf)=>{
            var model = (<any>window).Live2DModelWebGL.loadModel(buf);
            callback(model);
        });
    }

    //============================================================
    //    PlatformManager # loadTexture()
    //============================================================
    public loadTexture(model/*ALive2DModel*/, no:number, path:string, callback)
    { 
        // load textures
        var loadedImage = new Image();
        loadedImage.crossOrigin = 'Anonymous';
        loadedImage.src = path;
        
        var thisRef = this;
        loadedImage.onload = function() {
            // create texture
            var gl = getContext();
            var texture = gl.createTexture();	 
            if (!texture){ console.error("Failed to generate gl texture name."); return -1; }

            if(model.isPremultipliedAlpha() == false){
                // 
                gl.pixelStorei(gl.UNPACK_PREMULTIPLY_ALPHA_WEBGL, 1);
            }
            gl.pixelStorei(gl.UNPACK_FLIP_Y_WEBGL, 1);	
            gl.activeTexture(gl.TEXTURE0);
            gl.bindTexture(gl.TEXTURE_2D, texture);
            gl.texImage2D(gl.TEXTURE_2D, 0, gl.RGBA, gl.RGBA, 
                        gl.UNSIGNED_BYTE, loadedImage);
            gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.LINEAR);
            gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.LINEAR_MIPMAP_NEAREST);
            gl.generateMipmap(gl.TEXTURE_2D);


            
            model.setTexture(no, texture);
            
            // 
            texture = null;
            
            if (typeof callback == "function") callback();
        };
        
        loadedImage.onerror = function() { 
            console.error("Failed to load image : " + path); 
        }
    }


    //============================================================
    //    PlatformManager # parseFromBytes(buf)

    //============================================================
    public jsonParseFromBytes(buf){
        
        var jsonStr;
        
        
        
        var bomCode = new Uint8Array(buf, 0, 3);
        if (bomCode[0] == 239 && bomCode[1] == 187 && bomCode[2] == 191) {
            jsonStr = String.fromCharCode.apply(null, new Uint8Array(buf, 3));
        } else {
            jsonStr = String.fromCharCode.apply(null, new Uint8Array(buf));
        }
        
        var jsonObj = JSON.parse(jsonStr);
        
        return jsonObj;
    }


    //============================================================
    //    PlatformManager # log()
    //============================================================
    public log(txt:string)
    {
        //console.log(txt);
    }

    
}